.project-hero {
  padding: 12rem 0;
  height: 100vh;
  position: relative;
  z-index: 2;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  &__img {
    position: absolute;
    z-index: -1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;

    img {
      width: 100%;
      height: 100%;
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;
      width: 100%;
      height: 100%;

    }
  }

  &__container {
    position: relative;
    z-index: 2;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__title {
    color: var(--light1000);
  }

}